	module.exports = {"data":{"markdownRemark":{"html":"<h2>Actual Time Estimation</h2>\n<p>Actual time estimation is a time-based method of estimating development work.  </p>\n<p>It is critically important to be able to estimate the time-to-completion of an Agile project, unfortunately, it is also one of the most difficult parts of planning an Agile project. </p>\n<p>Its intent is to best approximate the amount of time required to complete a given development task. </p>\n<p>One technique you may use is to estimate the time it will take to complete user stories. When you are doing this, remember to consult with other members of your agile team. For each user story, make sure that you estimate a time that is realistic and feasible, yet not so much that the client is overcharged for simple work. Consult members of your team so that you may be able to leverage their expertise to be able to understand how much work is required and how long it will take. When you have a consensus for each user story, you can add up the times to come up with a time estimation. </p>\n<p>As the requirements of the project change over time, you can update the estimate. If a project loses some resources originally allocated to it, you may need to cut user stories to be able to complete them in the same amount of total time. Similarly, if you want to improve the quality of the app, you may need to allocate more time to each user story to make sure your team has time to finish the app with the desired quality.</p>\n<p>Generally, these estimates are calculated using ideal engineering hours.</p>\n<p>Examples: </p>\n<p><strong>This task will be complete in 10 days.</strong></p>\n<p>Or… </p>\n<p><strong>This task will be complete by January 10th.</strong></p>\n<p>Or… </p>\n<p><strong>This task will require 25 development hours for completion.</strong></p>\n<h3>More Information:</h3>\n<ul>\n<li>\n<p><a href=\"http://www.brighthubpm.com/agile/50212-the-agile-triangle-value-quality-and-constraints/\">Agile Constraints</a></p>\n</li>\n<li>\n<p><a href=\"http://info.thoughtworks.com/rs/thoughtworks2/images/twebook-perspectives-estimation_1.pdf\">How do you estimate on an Agile project?</a></p>\n</li>\n</ul>","fields":{"slug":"/agile/actual-time-estimation/"},"frontmatter":{"title":"Actual Time Estimation"}}},"pathContext":{"slug":"/agile/actual-time-estimation/"}}

